import React from 'react';
import NavBar from './components/NavBar';
import TypeWriter from 'react-typewriter';
import TypeIt from "typeit-react";
import FloatingButton from './components/FloatingButton';


const style = {
  margin: 0,
  top: 'auto',
  right: 20,
  bottom: 20,
  left: 'auto',
  position: 'fixed',
};

function App(){

  return (
    <div className="App">
            <TypeIt
              getBeforeInit={(instance) => {
                instance.type("Hi, I'm Sanchez. I'm a Software Engineer.").pause(750).delete(18).pause(500).type("Full Stack Developer.")
                .pause(750).delete(21).pause(500).type("Front-End Tinkerer.")
                .pause(750).delete(19).pause(500).type("Back-End Builder.");

                // Remember to return it!
                return instance;
              }}
            />;
            <FloatingButton style={style}/>
    </div>
  );
};

export default App;
